.hero-container{
    display: flex;
    align-items: center;
    position: relative;
}



.hero-content{
    flex: 1;
}

.hero-content h2{
    font-size: 3.8rem;
    font-weight: 600;
    line-height: 5rem;
    margin-bottom: 1rem;
}

.hero-content p{
    width: 80%;
    font-size: 1rem;
    font-weight: 400;
    line-height: 2rem;
}

.hero-content p span{
    color: #a020f0;
}

.hero-img{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    margin-top: 3rem;
}
.hero-img > div{
    display: flex;
    align-items: flex-end;
    gap: 2rem;
}

.hero-img > div > img{
    width: 25rem;
    height: 25rem;
    border-radius: 2rem;
    opacity: 70%;
    transition: all 0.3s ease;
}

.tech-icon{
    width: 4rem;
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    border-radius: 0.65rem;
    border: 1.5px solid #6852ba;
}

.tech-icon img{
    width: 3rem;
    height: auto;
    transition: all .3s ease;
}

.hero-img > div > img:hover,
.tech-icon img:hover{
    transform: translateY(-0.5rem);
}

@media (max-width:1025px){
    .hero-content h2{
        font-size: 3rem;
        line-height: 4rem;
    }
    .hero-content p{
        font-size: 0.9rem;
        line-height: 1.6rem;
    }
    .hero-img > div > img{
        width: 12rem;
    }
    .tech-icon{
        width: 4rem;
        height: 4rem;
    }
    .tech-icon img{
        width: 2.5rem;
    }
}

@media(max-width:768px){
    .hero-container{
        flex-direction: column;
    }
    .hero-img{
        margin: 2rem 0 2rem 0;
    }
    .hero-container h2{
        margin-top: 3rem;
        
    }
    .hero-content h2,
    .hero-content p{
        width: auto;
        text-align: center;
    }
    .hero-img > div > img{
        width: 20rem;
        height: 20rem;
    }

}

@media(max-width:600px){
    .hero-content h2{
        font-size: 2.2rem;
        line-height: 3rem;
    }
    .hero-content p{
        font-size: 0.8rem;
        line-height: 1.3rem;
    }
    .hero-img{
        gap: 1rem;
    }
    .hero-img > div > img{
        width: 14rem;
    }
    .tech-icon{
        width: 3.3rem;
        height: 3.3rem;
    }
    .tech-icon img{
        width: 2rem;
    }
}